* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

.log {
    font-family: Arial, Helvetica, sans-serif;
    background-image: url(tapiz.jpg);
    background-size:cover;
    min-height: 100vh;
    max-height: 100%;
    
    }


.form-login {
    
    width: 400px;
    height: 380px;
    background: #4e4d4d;
    margin: auto;
    box-shadow: 7px 13px 37px #000;
    padding: 20px 30px;
    border-top: 4px solid #017bab;
    color: white;
}

.form-login h1 {
    margin: 0;
    text-align: center;
    height: 40px;
    margin-bottom: 30px;
    border-bottom: 1px solid;
    font-size: 20px;
}

.controls {
    width: 100%;
    border: 1px solid #017bab;
    margin-bottom: 15px;
    padding: 11px 10px;
    background: #252323;
    font-size: 14px;
    font-weight: bold;
}

.buttons {
    width: 100%;
    height: 40px;
    background: #017bab;
    border: none;
    color: white;
    margin-bottom: 16px;
}

.form-login p {
    height: 40px;
    text-align: right;
    border-bottom: 1px solid;
    margin: 10px;
}

.login-google {
    text-align: center;
    font-size: 18px;       
}

.form-login a {
    color: white;
    text-decoration: none;
    font-size: 14px;
}

.form-login a:hover {
    text-decoration: underline;
}